.header {
    align-items: center;
}

.header h1 {
    flex-grow: 1;
    font-size: 2.1rem;
    white-space: nowrap;
}

.header h2 {
    font-size: 1.1rem;
    color: gray;
    text-align: right;
}
.todo-list-item {
    font-size: 1.25rem;
}

.todo-list-item button {
    width: 35px;
    margin: 3px;
}

.todo-list-item-label {
    margin-left: 1.25rem;
    line-height: 35px;
    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.todo-list-item.done .todo-list-item-label {
    text-decoration: line-through;
}

.todo-list-item.important .todo-list-item-label {
    font-weight: bold;
    color: steelblue;
}
.todo-list .list-group-item {
    padding: .25rem .75rem;
}
.search-input {
    width: auto;
    flex-grow: 1;
    margin-right: 3px;
}

.item-add-form {
    margin-top: 10px;
}

.btn-outline-secondary {
    flex-shrink: 0;
    margin-left: 10px;
}
.app {
    margin: 2rem auto 0 auto;
    max-width: 500px;
}

.panel {
    margin: 1rem 0;
}
